@-webkit-keyframes mymove {
0% {
    -webkit-transform: scale(1);
}
25% {
    -webkit-transform: scale(0.8);
}
50% {
    -webkit-transform: scale(1.1);
}
75% {
    -webkit-transform: scale(0.9);
}
100% {
    -webkit-transform: scale(1);
}
}
@keyframes mymove {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
25% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
}
50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
75% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
